export default {
	name: 'footer-tabbar',
	data() {
		return {
			TabID: 0, MsgDot: false,
		};
	},
	props: {
		tabID: {
			type: [String,Number],
			default: 0
		},
		msgDot: {
			type: Boolean,
			default: false
		},
	},
	created() {
		this.TabID = this.tabID;
		this.MsgDot = this.msgDot;
	},
	watch: {
		tabID() {
			if(this.tabID != 2) {
				this.TabID = this.tabID;
			}
		},
		msgDot() {
			this.MsgDot = this.msgDot;
		}
	},
	methods: {
		tabTap(index) {
			if(index != this.TabID) {
				this.$emit('tabTap', index);
			}
		}
	}
}
